import React, { Component } from 'react';

import echarts from "echarts/lib/echarts";
import 'echarts/lib/chart/line';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/legend';
import 'echarts/lib/component/grid';
import 'echarts/lib/component/dataZoom';

class HistoryEvaluateChart extends Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    componentWillReceiveProps(nextProps) {
        if (!nextProps.datas) return
        this.updateData(nextProps.datas)
    }

    componentDidMount() {
        const historyEvaluateChart = echarts.init(document.getElementById("historyEvaluate"))
        historyEvaluateChart.showLoading()
        this.historyEvaluateChart = historyEvaluateChart
    }

    updateData = (datas) => {
        this.historyEvaluateChart.setOption({
            tooltip: {
                trigger: 'axis',
                textStyle: {
                    fontSize: 18
                }
            },
            legend: {
                top: '10%',
                orient: 'vertical',
                icon: 'circle',
                itemWidth: 10,
                itemHeight: 10,
                data: ['保密性', '完整性', '可控性',],
                textStyle: {
                    color: '#fff',
                    fontSize: 18
                }
            },
            grid: {
                left: '2%',
                right: '2%',
                bottom: '0%',
                top: '5%',
                show: true,
                containLabel: true,
                backgroundColor: '#333'
            },
            dataZoom: [{
                type: 'slider',
                xAxisIndex: [0],
                filterMode: 'filter',
                start: 0,
                end: 100
            }],
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: datas[0],
                // data: [1,2,3],
                axisLabel: {
                    show: true,
                    margin: 17,
                    color: '#fff',
                    verticalAlign: 'bottom',
                    lineHeight: 10,
                }
            },
            yAxis: {
                type: 'value',
                axisLabel: {
                    color: '#fff'
                }
            },
            series: [
                {
                    name: '保密性',
                    type: 'line',
                    data: datas[1],
                    lineStyle: {
                        // color: '#13c2c2',
                        width: 5
                    }
                },
                {
                    name: '完整性',
                    type: 'line',
                    data: datas[3],
                    lineStyle: {
                        // color: '#30ff29',
                        width: 5
                    }
                },
                {
                    name: '可控性',
                    type: 'line',
                    data: datas[2],
                    lineStyle: {
                        // color: '#faad14',
                        width: 5
                    }
                },
            ]
        })
        this.historyEvaluateChart.hideLoading()
    }

    render() {
        return (
            <div className="HistoryEvaluate-chart table" id='historyEvaluate'></div>
        )
    }
}

export default HistoryEvaluateChart