import React, { Component } from 'react';

class VisitCountTable extends Component {
    constructor(props) {
        super(props)
        this.state = {
            visitCountTableResult: '',
        }
    }

    componentWillReceiveProps(nextProps) {
        if (!nextProps.datas) return
        this.setState({ visitCountTableResult: nextProps.datas })
    }

    render() {
        let { visitCountTableResult } = this.state
        const list = ['总访问量', '异常访问量', '攻击总数']
        return (
            <div className="typeTop">
                <div className="topHead">访问量统计</div>
                {
                    visitCountTableResult !== '' ?
                        (
                            <div className="topMain">
                                {
                                    visitCountTableResult.map((item, index) => {
                                        const percentage = visitCountTableResult[0] === '' ? null : visitCountTableResult[0] === 0 ? '0%' : `${(item / visitCountTableResult[0] * 100).toFixed(2)}%`
                                        return (
                                            <div className={index % 2 === 0 ? "diffTrColor" : null}>
                                                <span>{list[index]}</span>
                                                <span>
                                                    <span>{item}</span>&nbsp;/&nbsp;<span>{percentage}</span>
                                                </span>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        ) :
                        <div className='topMainNull'>暂无数据</div>
                }
            </div>
        )
    }
}

export default VisitCountTable