import React, { Component } from 'react';

// import ReactWordcloud from "react-wordcloud";
// import WordCloud from 'react-d3-cloud';
import 'echarts/lib/chart/tree';
import 'echarts/lib/component/tooltip';

class AttackLocate extends Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    render() {
        const SQLi_Xpath = ['\'', '"', '/', '\\', 'OR', 'AND', 'SELECT', 'UNION', 'LIKE', 'ALL', 'NULL', 'HEX', '<!--']
        const XSS_LDAPi = ['<script>', '<iframe', '<style', '<a>', 'href', '、', '&#', '@[|\\]^`', '^(#$!@#$)(()))******', ')(']
        const SSI_FormatString = ['<!--#include file=”', '<!--#include+file', '<!--#EXEC+cmd="', '<!--#exec cmd="', '%n', '%x', '%d']
        const lis = [
            {
                name: 'SQLi\\Xpath',
                value: SQLi_Xpath,
                deg: 360 / SQLi_Xpath.length,
            },
            {
                name: 'XSS\\LDAPi',
                value: XSS_LDAPi,
                deg: 360 / XSS_LDAPi.length,
            },
            {
                name: 'SSI\\FormatString',
                value: SSI_FormatString,
                deg: 360 / SSI_FormatString.length,
            }
        ]
        return (
            <div className="table">
                {
                    lis.map((it, i) => {
                        return (
                            <div className='wordBlock' key={i}>
                                <div className='blockHead' style={{order: i%2 === 1 ? 1 : 0}}>{it.name}</div>
                                <div className='blockBody'>
                                    <div className='circle'>
                                        {
                                            it.value.map((item, index) => {
                                                return (
                                                    <div
                                                        className='solid'
                                                        style={{ transform: `rotate(${-90 + Math.ceil(index * it.deg)}deg)` }}
                                                        key={index}
                                                    ></div>
                                                )
                                            })
                                        }
                                        {
                                            it.value.map((item, index) => {
                                                return (
                                                    <div
                                                        className='words'
                                                        style={{ transform: `rotate(${Math.ceil(index * it.deg)}deg)` }}
                                                        key={index}
                                                        title={item}
                                                    >
                                                        {item}
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                    <div className='copyCircle'></div>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
            // <ReactWordcloud className="AttackLocate-chart table" words={words} />

        )
    }
}

export default AttackLocate

