import React, { Component } from 'react';

import echarts from "echarts/lib/echarts";
import 'echarts/lib/chart/pie';
import 'echarts/lib/component/tooltip';

class AttackTypeChart extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  componentWillReceiveProps(nextProps) {
    this.updateData(nextProps.datas)
  }

  componentDidMount() {
    const attackTypeChart = echarts.init(document.getElementById("attackType"))
    attackTypeChart.showLoading()
    this.attackTypeChart = attackTypeChart
  }

  updateData = (datas) => {
    this.attackTypeChart.setOption({
      backgroundColor: '#27293D',
      tooltip: {
        trigger: 'item',
        formatter: "{a} <br/>{b} : {c} ({d}%)",
        textStyle: {
          fontSize: 18
        }
      },
      calculable: true,
      itemStyle: {
        color: '#fff',
        // borderColor: 'rgba(0, 0, 0, 1)'
      },
      series: [
        {
          name: '攻击类型',
          type: 'pie',
          radius: ['15%', '60%'],
          center: ['50%', '50%'],
          roseType: 'area',
          itemStyle: {
            color: '#fff',
            // borderColor: 'rgba(0, 0, 0, 1)'
          },
          label: {
            normal: {
              textStyle: {
                color: '#fff',
                fontSize: 22
              }
            }
          },
          // data: [{ value: 1, name: 'uio' }, { value: 7 }, { value: 4 }],
          // data: [],
          data: datas,
          animationType: 'scale',
          animationEasing: 'elasticOut',
          animationDelay: function (idx) {
            return Math.random() * 200;
          }
        }
      ]
    })
    this.attackTypeChart.hideLoading()
  }

  render() {
    return (
      <div className={this.props.class} id='attackType'></div>
    )
  }
}

export default AttackTypeChart